@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;200;300;400;500;600;700;800;900&display=swap');

body {
  font-family: 'Roboto Slab', serif;
}

.app {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100vh;
  background: linear-gradient(
      rgba(255, 255, 255, 0.7),
      rgba(255, 255, 255, 0.3)
    ),
    url('https://images.pexels.com/photos/114979/pexels-photo-114979.jpeg?auto=compress&cs=tinysrgb&dpr=2&w=500');
  background-size: cover;
  background-position: center;
}

form {
  width: 40%;
  background-color: white;
  padding: 0px 60px;
  border-radius: 10px;
}

h1 {
  color: rgb(77, 1, 77);
  text-align: center;
}

button {
  width: 100%;
  height: 50px;
  padding: 10px;
  border: none;
  background-color: rebeccapurple;
  color: white;
  border-radius: 5px;
  font-weight: bold;
  font-size: 18px;
  cursor: pointer;
  margin-top: 15px;
  margin-bottom: 30px;
  transition: 0.4s;
}

button:disabled {
  cursor: not-allowed;
  background-color: rgb(85, 80, 95);
  transition: 0.4s;
}

p#copyright {
  text-align: center;
  font-size: 16px;
  font-weight: 600;
  color: rgb(85, 80, 95);
  margin-top: 20px;
}

@media only screen and (max-width: 601px) {
  .app {
    height: 140vh;
  }

  form {
    width: 50%;
  }

  h1 {
    font-size: 20px;
  }

  button {
    font-size: 16px;
  }

  p#copyright {
    font-size: 10px;
  }
}
