.formInput {
  display: flex;
  flex-direction: column;
  width: 100%;
}

::placeholder {
  color: lightgray;
  opacity: 1; /* Firefox */
  font-size: 14px;
}

input {
  padding: 15px;
  margin: 10px 0px;
  border-radius: 5px;
  border: 1px solid gray;
}

label {
  font-size: 12px;
  color: gray;
}

.formInput span {
  font-size: 12px;
  padding: 3px;
  margin-bottom: 12px;
  color: red;
  display: none;
}

input:invalid[focused='true'] {
  border: 1px solid red;
}

input:invalid[focused='true'] ~ span {
  display: block;
}
