.textArea {
  display: flex;
  flex-direction: column;
  width: 100%;
}

textarea {
  height: 78px;
  padding: 15px;
  margin: 10px 0px;
  border-radius: 5px;
  border: 1px solid gray;
  resize: none;
  /* font-family: Arial, Helvetica, sans-serif; */
  font-family: 'Roboto Slab', serif;
}

#characterCounts {
  margin: 0;
  text-align: right;
  font-size: 14px;
  font-family: consolas;
}

@media only screen and (max-width: 601px) {
  #characterCounts {
    font-size: 14px;
  }
}
